}
.footer b {
  display: block;
  text-align: center;
  margin-top: 35px;
}
.footer b a {
  color: #fff;
  display: inline-block;
  margin: 0 15px;
  height: 37px;
  line-height: 37px;
  font-size: 28px;
}
.footer b a.a1 {
  padding-left: 55px;
