.pub-tit {
  text-align: center;
  padding: 55px 0;
  float: left;
  width: 100%;
}
.pub-tit strong {
  padding-left: 65px;
  background: url("../img/049b3_50x41.png") left center no-repeat;
  background-size: 50px;
  font-size: 40px;
  display: inline-block;
}
.pub-tit strong span {
