}
.ruguo {
  float: left;
  width: 100%;
  padding-bottom: 447px;
  position: relative;
}
.ruguo .scroll-view {
  position: relative;
  float: left;
  width: 100%;
}
.ruguo .scroll-view .pos {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  z-index: 2;
}
.ruguo .scroll-view .pos img {
  width: 50px;
}
.ruguo .scroll-view .pos.prev {
  left: 0;
}
.ruguo .scroll-view .pos.next {
  right: 0;
}
.ruguo .scroll-view ul li {
  width: 100%;
}
.ruguo .scroll-view ul li img {
  width: 100%;
}
.ruguo .look {
  width: 700px;
  position: absolute;
  left: 25px;
  bottom: 0;
}
.ruguo .look .more {
  display: block;
  margin: 0 auto;
  width: 300px;
  height: 56px;
  line-height: 56px;
  border-radius: 60px;
  background: #ffeded;
  text-align: center;
  color: #ff8989;
}
.ruguo .look .box {
  background: #fff url("../img/776d8_313x258.png") right bottom/313px no-repeat;
  box-shadow: 0 5px 10px rgba(255,176,187,0.6);
  float: left;
  width: 100%;
  margin-top: 32px;
  padding: 37px;
  border-radius: 20px;
}
.ruguo .look .box ul li {
  width: 100%;
  margin-top: 44px;
}
.ruguo .look .box ul li h3 {
  color: #333;
  position: relative;
  padding-left: 52px;
}
.ruguo .look .box ul li h3:after {
  content: '';
  position: absolute;
  width: 30px;
  height: 30px;
  background: url("../img/3623d_30x30.png") center top no-repeat;
  background-size: cover;
  left: 0;
}
.ruguo .look .box ul li p {
  line-height: 1.4;
  color: #9d9d9d;
  margin-top: 23px;
  padding-left: 52px;
  text-align: justify;
}
.my-look {
  padding: 60px 24px 50px;
  float: left;
  width: 100%;
}
.my-look h2 {
  font-weight: bold;
  font-size: 40px;
  background: -webkit-gradient(linear, 0% 0%, 0% 100%, from(#ff476d), to(#ff926c));
  color: transparent;
  -webkit-background-clip: text;
}
.my-look h2 span {
  font-size: 44px;
  display: block;
  color: #ffd7dc;
  opacity: 0.59;
  filter: alpha(opacity=59);
  text-transform: uppercase;
  font-weight: bold;
  position: relative;
  top: -15px;
  z-index: -1;
}
.my-look ul {
  margin-top: 20px;
}
.my-look ul li {
  width: 340px;
  box-shadow: 0 3px 10px rgba(255,176,187,0.59);
  border-radius: 10px;
}
.my-look ul li:nth-child(even) {
  float: right;
}
.my-look ul li img {
  width: 100%;
}
.kuaisu {
  float: left;
  width: 100%;
}
.kuaisu ul li {
  width: 100%;
  padding: 39px 20px;
}
.kuaisu ul li:nth-child(odd) {
  background: #fff0f2;
}
.kuaisu ul li .pict {
  width: 122px;
}
.kuaisu ul li .pict img {
  width: 100%;
}
.kuaisu ul li .text {
  width: calc(100% - 122px);
}
.kuaisu ul li .text.fr {
  padding-left: 20px;
}
.kuaisu ul li .text.fl {
  padding-right: 20px;
  text-align: right;
}
.kuaisu ul li .text h3 {
  font-size: 30px;
  color: #ff486d;
  margin-top: 25px;
}
.kuaisu ul li .text p {
  font-size: 24px;
  color: #ea8b98;
  line-height: 1.4;
  margin-top: 10px;
}
.header {
  background: -webkit-linear-gradient(top, #ff406d, #ff926c);
  float: left;
  width: 100%;
  border-radius: 0 0 0 120px;
}
.header .top {
  float: left;
  width: 100%;
  padding: 40px 40px 0;
}
.header .top .fl h2 {
  font-size: 28px;
  padding-left: 55px;
  background: url("../img/8fe9b_45x37.png") left center no-repeat;
  line-height: 55px;
  background-size: 45px;
  color: #fff;
}
.header .top .fr h4 {
  line-height: 55px;
  padding-left: 35px;
  background: url("../img/2e72e_25x29.png") left center no-repeat;
  background-size: 25px;
  color: #fff;
  float: left;
}
.header .top .fr p {
  float: left;
  line-height: 29px;
  width: 215px;
  height: 55px;
  border-radius: 50px;
  background: #f14164;
  margin-left: 30px;
}
.header .top .fr p a {
  float: left;
  color: #fff;
  width: 50%;
  text-align: center;
  line-height: 55px;
}
.header .top .fr p a:last-child {
  position: relative;
}
.header .top .fr p a:last-child:after {
  content: '';
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  left: 0;
  width: 1px;
  height: 20px;
  background: #fff;
}
.header .text {
  float: left;
  width: 100%;
  padding: 20px 22px 0;
}
.header .text .fl {
  margin-top: 33px;
  width: 490px;
  overflow: hidden;
}
.header .text .fl h1 {
  font-size: 62px;
  color: #fff;
  line-height: 1.1;
}
.header .text .fl p {
  background: #ef615c;
  color: #fff;
  line-height: 50px;
  padding: 0 20px;
  margin-top: 40px;
  font-size: 22px;
  border-radius: 50px;
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  transform: scale(0.9);
  transform-origin: left top;
  text-align: center;
}
.header .text .fr {
  width: 212px;
}
.header .text .fr img {
  width: 100%;
}
